import { Button as ButtonComponent, ButtonSize, ButtonColor } from 'im.v2.component.elements';
import { SupervisorBaseMessage } from 'im.v2.component.message.supervisor.base';
import type { ImModelMessage } from 'im.v2.model';

import { metaData } from './const/tools';
import './css/update-features.css';

import type { SupervizorComponentParams } from 'im.v2.const';

const TOOL_ID_PARAMS_KEY = 'toolId';
// @vue/component
export const SupervisorTariffExpansionMessage = {
	name: 'SupervisorTariffExpansionMessage',
	components: { ButtonComponent, SupervisorBaseMessage },
	props: {
		item: {
			type: Object,
			required: true,
		},
		dialogId: {
			type: String,
			required: true,
		},
	},
	computed:
	{
		message(): ImModelMessage
		{
			return this.item;
		},
		ButtonSize: () => ButtonSize,
		ButtonColor: () => ButtonColor,
		toolId(): string
		{
			return this.message.componentParams[TOOL_ID_PARAMS_KEY];
		},
		toolData(): SupervizorComponentParams
		{
			return metaData[this.toolId];
		},
		modifierImageClass(): string
		{
			return `--${this.toolId}`;
		},
	},
	methods:
	{
		loc(phraseCode: string, replacements: {[p: string]: string} = {}): string
		{
			return this.$Bitrix.Loc.getMessage(phraseCode, replacements);
		},
	},
	template: `
		<SupervisorBaseMessage
			:item="item"
			:dialogId="dialogId"
			:title="loc(toolData.title)"
			:description="loc(toolData.description)"
		>
			<template #image>
				<div :class="['bx-im-message-update-features__image-wrapper', modifierImageClass]" />
			</template>
			<template #actions>
				<ButtonComponent
					:size="ButtonSize.L"
					:isRounded="true"
					:color="ButtonColor.Success"
					:text="loc(toolData.detailBtn.text)"
					@click="toolData.detailBtn.fn"
				/>
				<ButtonComponent
					:size="ButtonSize.L"
					:color="ButtonColor.LightBorder"
					:isRounded="true"
					:text="loc(toolData.infoBtn.text)"
					@click="toolData.infoBtn.fn"
				/>
			</template>
		</SupervisorBaseMessage>
	`,
};
